@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  background-color: rgba(240, 224, 224, 0.623);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    width: 800px;
    height: 520px;
    background-color: #1234e13D;
    position: fixed;
    top: 50%;
    left: 50%;
     /* bring your own prefixes */
    transform: translate(-50%, -50%);
    color: white;
    border-radius: 40px;
    font-family: 'Montserrat', sans-serif;
}

.child {
    margin: 0%;
}

.current-location {
    font-size: 16px;
    margin-left: 52px;
    color: #596176;
    font-weight: 800;
    margin-bottom: 0;
    margin-top: 72px;
}

.header {
    font-size: 48px;
    font-weight: 800;
    margin-top: -10px;
    margin-left: 48px;
    color: #1233E1;
}

.flex {
    display: flex;
    justify-content: space-between;
    margin-left: 80px;
    margin-right: 80px;
}

.temp {
    font-size:100px;
    margin: 0;
    color: black;
    font-weight: 800;
}

.description {
    font-size: 36px;
    color: #1233E1;
    font-weight: 600;   
}

.wind {
    font-size: 36px;
    margin-bottom: 0;
    margin-top: 36px;
    margin-left: 80px;
    color: black;
    font-weight: 700;
}

.humidity {
    font-size: 36px;
    margin-left: 80px;
    margin-bottom: 72px;
    color: black;
    font-weight: 700;
}
.header-weather {
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    width: 100%;
    height: 1%;
    position: fixed;
    margin-left: 80px;
    margin-top: 30px;
}

